@font-face {
  font-family: "Merienda", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Merienda&family=Raleway:wght@300&display=swap");
  font-family: "Raleway", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap");
}
body {
  background-color: #272424;
  color: white;
  margin: 0px;
}

.part {
  padding: 30px 90px;
}

.part-title {
  color: #4f5b66;
  font-size: 2em;
  margin-bottom: 30px;
}

a:hover {
  color: #f1da0c;
}

header {
  overflow: hidden;
  position: relative;
  margin: 25px 50% 0px 0px;
}
header ul {
  padding-left: 5rem;
  display: flex;
}
header ul li {
  flex-direction: row;
  list-style-type: none;
  padding: 14px 25px;
  font-size: 34px;
}
header ul a {
  text-decoration: none;
  color: white;
}
header #link {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  z-index: 1;
  top: 350px;
  left: 15px;
}
header #link hr.vertical {
  width: 2px;
  height: 120px;
  color: white;
  border-right: 1px dashed white;
  border-top: 0;
  border-left: 0;
  border-bottom: 0;
}
header .social {
  font-size: 30px;
  margin: 10px 15px;
}
header .social:hover {
  color: #f1da0c;
}

#home {
  background: center bottom no-repeat url("../gulp/img/nature2.webp");
  z-index: 0;
  background-size: cover;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}
#home button {
  margin: auto;
  background-color: #620d9b;
  width: 150px;
  height: 50px;
  border: 1px, solid gray;
  border-radius: 50px;
  color: white;
  font-size: 1.5em;
  font-weight: bold;
  text-decoration: none;
}
#home button:hover {
  cursor: pointer;
}

#home-title h1 {
  font-family: "Raleway", sans-serif;
  text-align: center;
  padding-top: 10%;
  font-size: 4em;
}
#home-title h2 {
  text-align: center;
}

#about-container {
  display: flex;
  flex-direction: row;
}

#about-text {
  padding-left: 12vw;
  font-size: larger;
}

#projects-container {
  display: grid;
  grid-template-columns: repeat(3, 450px);
  grid-template-rows: repeat(2, 500px);
}

.project {
  text-align: center;
  background-color: #292b2e;
  border: 2px solid #464442;
  border-radius: 10px;
  width: 350px;
  height: 450px;
}
.project h3 {
  font-size: 2em;
}
.project p {
  margin-top: 35px;
}

.project:hover {
  border: 2px solid #f1a90c;
  transform: scale3d(1.1, 1.1, 1.1);
  box-shadow: -5px 5px 10px 10px #000000;
}

.items {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  height: 70px;
  padding-top: 120px;
}
.items .svg-mongo {
  padding-top: 15px;
}
.items .svg-express {
  padding-top: 15px;
}
.items .svg-js {
  padding-top: 15px;
}
.items .fa-sass {
  font-size: 3em;
  padding-top: 20px;
  color: #ff0d4f;
}
.items .fa-gulp {
  font-size: 3em;
  padding-top: 20px;
  width: 65px;
  color: red;
}
.items .fa-php {
  font-size: 3em;
  padding-top: 20px;
  color: #1f5fe9;
}
.items .fa-wordpress {
  font-size: 3em;
  padding-top: 20px;
  color: #1f57cf;
}
.items .fa-react {
  font-size: 3em;
  padding-top: 20px;
  color: #1665be;
}
.items .fa-node-js {
  font-size: 3em;
  padding-top: 20px;
  width: 65px;
  color: #1ee417;
}

.project-link-container {
  display: flex;
  justify-content: space-around;
  padding-top: 40px;
}
.project-link-container a {
  color: #c0c5ce;
  font-size: 1.5em;
}

.project-link-git:hover > .fa-folder-open {
  display: block;
}
.project-link-git:hover > .fa-folder {
  display: none;
}

.fa-folder-open {
  display: none;
}

.project-link-net:hover > .fa-sign-out-alt {
  display: block;
}
.project-link-net:hover > .fa-sign-in-alt {
  display: none;
}

.fa-sign-out-alt {
  display: none;
}

#contact, footer {
  background-color: black;
  display: block;
}

#contacts-container {
  display: flex;
  flex-direction: row;
}

#contacts-container-left, #contacts-container-right {
  width: 50%;
}

#contacts-container-left {
  padding-left: 60px;
}

.contact-box {
  background-color: #292b2e05;
  text-align: center;
  margin: 20px;
  padding: 10px;
  width: 250px;
  border-right: 0.7px solid #f1a90c;
  border-bottom: 0.7px solid #f1a90c;
  border-radius: 45px 0px 45px;
}
.contact-box h4 {
  color: #4f5b66;
  font-size: 1.2em;
  margin-top: 5px;
}
.contact-box svg {
  color: #620d9b;
}
.contact-box a {
  color: white;
  text-decoration: none;
  font-size: 1.2em;
}

.contact-box:hover {
  transform: translateX(70px);
  background-color: #a7adba;
}

#contacts-container-right {
  text-align: center;
}
#contacts-container-right h3 {
  font-size: 2em;
}

#contacts-container-right-button {
  display: flex;
}
#contacts-container-right-button button {
  margin: auto;
  background-color: #620d9b;
  width: 150px;
  height: 50px;
  border: 1px, solid gray;
  border-radius: 50px;
}
#contacts-container-right-button button a {
  color: white;
  font-size: 1.5em;
  font-weight: bold;
  text-decoration: none;
}

footer {
  text-align: center;
}
footer p {
  margin: 0;
  padding: 20px 0px;
}